import { createRouter, createWebHistory } from "vue-router";
import Home from "@/components/pages/Home.vue";

export const routes = createRouter({
  history: createWebHistory(),
  routes: [
    { path: "/", name: "Home", component: Home },
    {
      path: "/users",
      name: "Users",
      component: () => import("@/components/pages/Users.vue"),
    },
    {
      path: "/users/:id",
      name: "UsersItem",
      component: () => import("@/components/pages/UserItem.vue"),
    },
    {
      path: "/todos",
      name: "TodosPage",
      component: () => import("@/components/pages/Todos.vue"),
    },
    {
      path: "/posts",
      name: "PostsPage",
      component: () => import("@/components/pages/Posts.vue"),
    },
    {
      path: "/:catchAll(.*)",
      name: "404",
      component: () => import("@/components/pages/NoteFound.vue"),
    },
  ],
});
