import { createStore } from "vuex";

export const store = createStore({
  state: {
    todos: null,
    pens: null,
    keys: null,
  },
  mutations: {
    getPens(state, cook) {
      state.pens = cook;
    },
    getTodos(state,data) {
      state.todos = data;
    },
    getPosts(state,date) {
      state.keys = date;
    } 
  },
  actions: {
    async getPens(context) {
      let pen = await fetch("https://jsonplaceholder.typicode.com/users");
      let cook = await pen.json();
      context.commit("getPens", cook);
    },
    async getTodos(context) {
      let tod = await fetch("https://jsonplaceholder.typicode.com/todos");
      let data = await tod.json();
      context.commit("getTodos", data);

    },
    //
    async getPosts(context) {
      let pos = await fetch("https://jsonplaceholder.typicode.com/posts");
      let date = await pos.json();
      context.commit("getPosts", date);
    },
  },
  getters: {
    
  },
});
